#wrapper {
  height: 100vh;
  width: 100vw;
  margin: 0 auto;
}

#quote-box {

  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 450px;
  padding: 40px 50px;
  background-color: #fff;
}

i {
  font-size: 1em;
  margin-right: 0.4em;
}

.quote-text{
    text-align: center;
    width: 450px;
    height: auto;
    clear: both;
    font-weight: 500;
    font-size: 1.75em;
}

.quote-author {
    width: 450px;
    height: auto;
    clear: both;
    padding-top: 20px;
    font-size: 1em;
    text-align: right;
}

.buttons {
    width: 450px;
    margin: auto;
    display: block;
}
.button {
      height: 38px;
      border: none;
      border-radius: 3px;
      color: #fff;
      background-color: #333;
      outline: none;
      font-size: 0.85em;
      padding: 8px 18px 6px 18px;
      margin-top: 30px;
      cursor: pointer;
}
.button :hover {
  opacity: 0.6;
}

#tweet-quote {
        float: left;
        padding: 0px;
        padding-top: 8px;
        text-align: center;
        font-size: 1.2em;
        margin-right: 5px;
        height: 30px;
        width: 55px;
}
#new-quote {
   float: right;
}
a {
  text-decoration: none
}